(() => {
  const headerOpen = document.querySelector(`.header__open`);
  const navClose = document.querySelector(`.nav__close`);
  const nav = document.querySelector(`.nav`);
  const headerMessangers = document.querySelector(`.header__messangers`);
  const headerContactsToggle = document.querySelector(`.header__contacts-toggle`);

  window.addEventListener(`resize`, () => {
    if (window.matchMedia(`(max-width: 1023px)`).matches) {
      nav.inert = true;
      navClose.inert = true;
    } else {
      nav.inert = false;
      navClose.inert = false;
    }
  });

  function onNavEscPress(e) {
    if (e.key === `Escape`) {
      e.preventDefault();
      closeNav();
    }
  }

  function openNav() {
    nav.classList.remove(`nav--closed`);
    nav.classList.add(`nav--opened`);
    nav.inert = false;
    navClose.inert = false;
    document.addEventListener(`keydown`, onNavEscPress);
  }

  function closeNav() {
    nav.classList.remove(`nav--opened`);
    nav.classList.add(`nav--closed`);
    nav.inert = true;
    navClose.inert = true;
    document.removeEventListener(`keydown`, onNavEscPress);
  }

  headerOpen.addEventListener(`click`, (event) => {
    event.preventDefault();
    if (nav.classList.contains(`nav--closed`)) {
      openNav();
    }
  });

  navClose.addEventListener(`click`, (event) => {
    event.preventDefault();
    if (nav.classList.contains(`nav--opened`)) {
      closeNav();
    }
  });

  window.addEventListener(`resize`, () => {
    if (window.matchMedia(`(max-width: 799px)`).matches) {
      headerMessangers.inert = true;
      headerContactsToggle.inert = false;
    } else {
      headerMessangers.inert = false;
      headerContactsToggle.inert = true;
    }
  });

  function openContacts() {
    headerOpen.inert = true;
    headerMessangers.classList.remove(`header__messangers--closed`);
    headerMessangers.classList.add(`header__messangers--opened`);
    headerContactsToggle.classList.remove(`header__contacts-toggle--closed`);
    headerContactsToggle.classList.add(`header__contacts-toggle--opened`);
  }

  function closeContacts() {
    headerOpen.inert = false;
    headerMessangers.classList.remove(`header__messangers--opened`);
    headerMessangers.classList.add(`header__messangers--closed`);
    headerContactsToggle.classList.remove(`header__contacts-toggle--opened`);
    headerContactsToggle.classList.add(`header__contacts-toggle--closed`);
  }

  headerContactsToggle.addEventListener(`click`, (event) => {
    event.preventDefault();
    if (headerContactsToggle.classList.contains(`header__contacts-toggle--closed`)) {
      openContacts();
    } else if (headerContactsToggle.classList.contains(`header__contacts-toggle--opened`)) {
      closeContacts();
    }
  });
})();
